 *************************************************/
/* General styles
---------------------------------------*/
* {
  margin: 0;
  padding: 0;
}

html {
  min-height: 100%;
  height: 100%;
  font-size: 100%;
}

:-ms-input-placeholder, :-moz-placeholder, ::-moz-placeholder, ::-webkit-input-placeholder {
  color: #555;
}

ol, ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

.h1, .h2, .h3, .h4, h1, h2, h3, h4, p {
  line-height: normal;
  margin: 0;
  padding: 0;
}

a {
  color: rgba(0, 0, 0, 0.71);
  text-decoration: none;
}

a:hover {
  color: #eb601b;
  text-decoration: none;
}

a:focus, button:focus, input:focus, textarea:focus {
  outline: 0;
  text-decoration: none;
}

body {
  position: relative;
  height: 100%;
  font-family: Roboto;
  line-height: normal;
  color: #464646;
  background-color: #fff;
  padding: 0 !important;
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

main {
  position: relative;
  flex: 1 0 auto;
  display: flex;
  flex-direction: row;
  height: 100%;
}

footer {
  flex: 0 0 auto;
}

.header {
  flex: 0 0 auto;
  position: relative;
  z-index: 40;
  width: 220px;
  background-color: #2a2d2e;
  box-shadow: 4px 0 5px 0 rgba(0, 0, 0, 0.14), 1px 0 10px 0 rgba(0, 0, 0, 0.12), 2px 0 4px -1px rgba(0, 0, 0, 0.3);
}

.header__branding {
  padding: 5px 15px;
}

.header__logo {
  display: block;
  max-width: 190px;
  width: 100%;
  padding: 10px 0;
  margin: 0 auto;
}

.header__logo > img {
  width: 100%;
  height: 100%;
}

.header__welcome {
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  color: #fff;
}

.header__user_tools {
  position: relative;
  padding: 20px 15px;
}

.header__user_tools::before, .header__user_tools::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  width: calc(100% - 30px);
  height: 1px;
  background-color: #444;
  margin: 0 auto;
}

.header__user_tools::before {
  top: 0;
}

.header__user_tools::after {
  bottom: 0;
}

.header__user_tools ul {
  padding: 10px 0 0 !important;
}

.header__user_tools ul > li {
  display: block;
  padding: 0;
}

.header__user_tools ul > li > a {
  display: block;
  font-weight: 400;
  font-size: 13px;
  line-height: 32px;
  color: #fff;
  padding: 0;
}

.header__user_tools ul > li > a:hover {
  color: #eb601b;
}

.header__nav {
  position: relative;
  width: 100%;
  padding: 8px 0 !important;
}

.header__nav::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: calc(100% - 30px);
  height: 1px;
  background-color: #444;
  margin: 0 auto;
}

.header__nav:last-child::after {
  display: none;
}

.header__nav li {
  position: relative;
  display: block;
  padding: 0;
}

.header__nav li > a {
  display: block;
  font-weight: 400;
  font-size: 13px;
  line-height: 32px;
  color: #fff;
  padding: 4px 15px;
  transition: color 150ms ease, background-color 150ms ease;
}

.header__nav li:hover > a {
  color: #fff;
  background-color: #3f3f46;
}

.header__nav li:hover > .sub-menu {
  display: block;
}

.header__nav .sub-menu {
  display: none;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 100%;
  width: 180px;
  background-color: #333;
  box-shadow: 2px 0 2px 0 rgba(0, 0, 0, 0.14), 1px 0 5px 0 rgba(0, 0, 0, 0.12), 3px 0 1px -2px rgba(0, 0, 0, 0.2);
}

.header__nav .sub-menu > li > a {
  color: #fff;
}

.header__nav .sub-menu > li:hover > a {
  color: #fff;
  background-color: #3f3f46;
}

.footer {
  display: none;
}

.content {
  position: relative;
  flex: 1 1 auto;
  overflow-y: scroll;
}

.content__container {
  position: relative;
  padding: 20px;
}

.breadcrumbs {
  width: 100%;
  height: 65px;
  background-color: #2d2d2d !important;
  padding: 0 !important;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.breadcrumbs ul {
  display: flex;
  height: 100%;
  font-size: 0;
  padding: 0 25px;
}

.breadcrumbs ul li {
  display: inline-flex;
  align-items: center;
  height: 100%;
  font-weight: 500;
  font-size: 14px;
  line-height: 32px;
  color: #fff;
  padding: 0;
}

.breadcrumbs ul li::before {
  content: '';
  width: 6px;
  height: 6px;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  margin: 0 15px;
  transform: rotate(135deg);
}

.breadcrumbs ul li:first-child::before {
  display: none;
}

.breadcrumbs ul li a {
  display: inline-flex;
  align-items: center;
  height: 100%;
  color: #fff;
}

.breadcrumbs ul li a:hover, .breadcrumbs ul li a:focus {
  color: #eb601b;
}

.x_title {
  font-weight: 400;
  font-size: 34px;
  line-height: 52px;
  color: rgba(0, 0, 0, 0.71);
  margin-bottom: 10px;
}

.x_button {
  display: inline-block;
  float: none;
  width: 50px;
  height: 50px;
  font-size: 0;
  border: 0;
  border-radius: 50% !important;
  background-color: #9dc02e !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: 20px 20px !important;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  transition: background-color 150ms ease, box-shadow 150ms ease;
}

.x_button:hover, .x_button:focus {
  background-color: #eb601b !important;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
}

.x_button--add {
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
}

.x_button--grid {
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
}

.x_button--tree {
  background-size: 22px 22px !important;
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
}

.x_button--history {
  background-size: 22px 22px !important;
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
}

.x_button--view {
  background-size: 24px 24px !important;
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
}

.applications {
  display: flex;
  flex-direction: row;
  margin: -30px -15px;
}

.applications__column {
  padding: 0 15px;
}

.applications > div:not(.applications__column) {
  display: none;
}

.app {
  border: 0;
  border-radius: 4px;
  background-color: #fff;
  margin: 30px 0;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  transition: box-shadow 150ms ease;
}

.app:hover {
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
}

.app__title {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  color: #fff !important;
  background-color: #383838;
  padding: 2px 20px;
  transition: background-color 400ms ease;
}

.app__models {
  padding: 6px 20px;
}

.app__models__group {
  display: flex;
  flex-direction: row;
}

.app__models__title {
  flex: 1 1 auto;
  font-weight: 400;
  font-size: 13px;
  line-height: 32px;
  color: rgba(0, 0, 0, 0.71) !important;
}

.app__models__add {
  font-weight: 400;
  font-size: 13px;
  line-height: 32px;
  color: rgba(0, 0, 0, 0.71) !important;
  padding: 0 15px;
}

.app__models__change {
  font-weight: 400;
  font-size: 13px;
  line-height: 32px;
  color: rgba(0, 0, 0, 0.71) !important;
}

.app__models__title, .app__models__add, .app__models__change {
  background-position-y: center;
}

.app__models__title:hover, .app__models__add:hover, .app__models__change:hover {
  color: #eb601b !important;
}

.module {
  width: 100%;
  background-color: transparent;
}

.module table {
  width: 100%;
}

#content-related {
  max-width: 290px;
  width: 100%;
  float: none;
  margin: 30px 0 0 !important;
}

#content-related .module {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

#content-related h3 {
  margin-bottom: 0;
}

#content-related .actionlist {
  padding: 16px;
  margin: 0 !important;
}

ul.messagelist {
  position: absolute;
  z-index: 10;
  top: 10px;
  right: 15px;
}

ul.messagelist > li {
  color: #fff;
  border: 0;
  border-radius: 4px;
  padding-left: 40px;
  background-color: #3f3f46;
  background-position: 15px 12px;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.3);
}

ul.messagelist > li > a {
  color: #9dc02e;
}

ul.messagelist > li > a:hover {
  color: #eb601b;
}

.object_tools {
  position: absolute;
  top: 0;
  right: 30px;
  display: flex;
  flex-direction: row;
  float: none;
  padding: 0;
  margin: 0;
  transform: translateY(-50%);
}

.object_tools > li {
  display: inline-block;
  margin-right: 20px;
}

.object_tools > li:last-child {
  margin-right: 0;
}

.object_tools > li > a {
  display: block;
}

.fieldsets {
  border: 0;
}

.fieldsets__nav {
  display: flex;
  align-items: center;
  height: 48px;
  background-color: #383838;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.3);
  padding: 0 20px;
}

.fieldsets__nav > li {
  display: inline-block;
  height: 100%;
  padding: 0;
}

.fieldsets__nav > li > a {
  display: flex;
  align-items: center;
  height: 100%;
  font-weight: 500;
  font-size: 14px;
  line-height: 32px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.7);
  border: 0;
  border-bottom: 2px solid transparent;
  padding: 4px 15px;
  transition: color 150ms ease, border-color 150ms ease;
}

.fieldsets__nav > li > a:hover {
  color: #fff;
}

.fieldsets__nav > li > a.active {
  color: #fff;
  border-color: #eb601b;
}

.fieldsets__content {
  padding: 10px 0;
}

.submit_column {
  position: fixed;
  z-index: 50;
  right: 20px;
  bottom: 20px;
  display: flex;
  flex-direction: column;
  border: 0;
  background-color: transparent;
  padding: 10px;
  margin: 0;
}

.submit_column__button, .submit_column__button--save, .submit_column__button--stand, .submit_column__button--next, .submit_column__button--delete {
  display: block;
  width: 50px;
  height: 50px;
  font-size: 0;
  border: 0;
  border-radius: 50% !important;
  background-color: #9dc02e !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: 22px 22px !important;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
  margin: 0;
  margin-bottom: 10px;
  transition: background-color 150ms ease, box-shadow 150ms ease;
}

.submit_column__button:hover, .submit_column__button--save:hover, .submit_column__button--stand:hover, .submit_column__button--next:hover, .submit_column__button--delete:hover, .submit_column__button:focus, .submit_column__button--save:focus, .submit_column__button--stand:focus, .submit_column__button--next:focus, .submit_column__button--delete:focus {
  background-color: #eb601b !important;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.3);
}

.submit_column__button--save {
  font-size: 0;
  background-color: #0b381d !important;
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
  margin: 0;
  float: none !important;
}

.submit_column__button--stand {
  font-size: 0;
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
  float: none !important;
}

.submit_column__button--next {
  font-size: 0;
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
  float: none !important;
}

.submit_column__button--delete {
  background-color: #CC3434 !important;
  background-image: url(data:image/svg+xml;utf8;base64,) !important;
}

.submit_column__button--delete:hover, .submit_column__button--delete:focus {
  background-color: #d65c5c !important;
}

.submit_column__popup {
  position: absolute;
  right: 100%;
  font-weight: 400;
  font-size: 14px;
  color: #fff;
  border: 0;
  border-radius: 5px;
  background-color: #333;
  padding: 5px 8px;
  opacity: 0;
  white-space: nowrap;
  transition: opacity 150ms ease;
}

#tree.block-style ul.jqtree-tree .jqtree-element {
  background-color: rgba(56, 56, 56, 0.1) !important;
  transition: background-color 200ms ease, box-shadow 200ms ease;
}

#tree.block-style ul.jqtree-tree .jqtree-element .jqtree-toggler {
  transition: color 200ms ease;
}

#tree.block-style ul.jqtree-tree li.jqtree-selected > .jqtree-element {
  background-color: #383838 !important;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.3);
}

#tree.block-style ul.jqtree-tree li.jqtree-selected > .jqtree-element .jqtree-title, #tree.block-style ul.jqtree-tree li.jqtree-selected > .jqtree-element .edit {
  color: #fff !important;
  outline: none;
}

#tree.block-style ul.jqtree-tree li.jqtree-selected > .jqtree-element .jqtree-title:hover, #tree.block-style ul.jqtree-tree li.jqtree-selected > .jqtree-element .edit:hover {
  color: #eb601b !important;
}

#tree.block-style ul.jqtree-tree li.jqtree-selected > .jqtree-element .jqtree-toggler {
  color: #fff;
}

.form-row .filerFile .related-lookup, .form-row .filerFile .related-lookup ~ strong {
  display: none !important;
}

.form-row .filerFile .js-related-lookup.related-lookup {
  display: inline-block !important;
}

.login {
  background-color: #f2f2f2;
}

.login main {
  flex: 0 1 auto;
  flex-direction: column;
  max-width: 480px;
  width: 100%;
  height: auto;
  border: 0;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  padding: 0;
  margin: auto;
  overflow: hidden;
}

.login .header {
  width: 100%;
  box-shadow: 0 0 0 0 transparent;
}

.login #content-main form label {
  margin: 0;
}

.login #content-main form .submit-row {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1em 0;
}

.login .content {
  padding: 0 30px;
}
